<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>

  .money {
    height: 90px;
    line-height: 85px;
    background-color: white;
    background-image: linear-gradient(#e1e0e0 1px, transparent 1px, transparent);
    background-size: 5px 5px;
    margin: 5px 20px auto;
    text-align: center;
    font-size: 58px;
    font-weight: 500;

    span {
      vertical-align: text-top;
      font-size: 40px;
      line-height: 60px;
