  }

  .btn-borrow {
    display: inline-block;
    width: 95px;
    height: 95px;
    line-height: 95px;
    color: white;
    background-color: #e7bc2b;
    margin: 25px 0;
    border-radius: 50%;
  }

  .cell-unit {
    vertical-align: text-top;
    font-size: 18px;
    line-height: 24px;
  }
</style>



